.filter {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: var(--d16);

  max-width: 360px;

  @media screen and (min-width: 576px) {
    max-width: 530px;
  }

  @media screen and (min-width: 992px) {
    max-width: fit-content;
  }

  > fieldset {
    border-radius: var(--d8);
    padding: var(--d4);
    display: flex;
    justify-content: center;

    border: var(--d4) solid var(--light-red);
    > legend {
      font-size: var(--fz18);
      font-weight: var(--fw7);
      padding: 0 var(--d4);
      color: var(--red);

      @media screen and (min-width: 576px) {
        font-size: var(--fz20);
      }
      @media screen and (min-width: 992px) {
        font-size: var(--fz22);
      }
    }

    > select {
      font-size: var(--fz16);
      background-color: transparent;
      color: var(--white);
      outline: none;
      border: none;

      &::-webkit-scrollbar {
        width: var(--d4);
        background-color: var(--gray);
      }

      &::-webkit-scrollbar-thumb {
        border-radius: var(--d4);
        background: var(--gray);

        &:hover {
          background: var(--light-red);
        }
      }

      @media screen and (min-width: 576px) {
        font-size: var(--fz18);
      }
      @media screen and (min-width: 992px) {
        font-size: var(--fz20);
      }

      > option {
        background-color: var(--black);
      }
    }

    > input {
      color: var(--white);
      background-color: transparent;
      outline: none;
      border: none;
      font-size: var(--fz16);
      width: 50%;
      max-width: 150px;

      &::-webkit-outer-spin-button,
      &::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }

      &::placeholder {
        color: var(--white);
        opacity: 0.8;
      }

      @media screen and (min-width: 576px) {
        font-size: var(--fz18);
      }
      @media screen and (min-width: 992px) {
        font-size: var(--fz20);
      }
    }
  }

  > button {
    height: fit-content;
    padding: 0;
    border: none;
    outline: none;
    background-color: transparent;

    transition: transform var(--time) var(--time-des);

    > svg {
      color: var(--light-red);
      transition: color var(--time) var(--time-des);
    }

    &:hover,
    &:focus {
      transform: scale(1.1);

      > svg {
        color: var(--red);
      }
    }

    &:active {
      > svg {
        color: var(--white);
      }
    }
    &:disabled {
      transform: scale(1);

      > svg {
        color: var(--gray);
        cursor: auto;
      }
    }
  }
}
